html {
    scroll-behavior: smooth;
}

body {
	background: #fefefe;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
	font-size: 18px;
	font-weight: 400;
	color: #555;
	line-height: 1.7;
	text-rendering: optimizeLegibility;
	height: 100%
}

#side .nav-link.active,
#side .nav-link:hover,
#side .nav-link:focus{
    color: #111;
    border-bottom: 1px #000 dashed;
}

.sidebar{
    padding-right: 0;
    padding-left: 0;
}

.main {
    padding-right: 0px;
    padding-left: 0px;
}

@media (min-width:576px) {
    .sidebar {
        padding-right: 15px;
        
    }
}


@media (min-width:576px) {

}

@media (min-width:800px) {

}

@media (min-width:992px) {
    .edu-date {
        float: right;
    }
} 

@media (min-width:1100px) {

}

@media (min-width:1200px) {
    .container-fluid {
        max-width: 1200px;
    }
} 

.row {
    margin-left: 0;
    margin-right: 0;
}

.container-fluid {
    padding-right: 0;
    padding-left: 0;
}

.dashed {
    border: none;
    border-bottom: 1px dashed #ddd;
}

.author-container {
    position: sticky;
    top: 0px;
    padding-bottom: 10px;
    border-top: 10px solid #ffc107;
}



.page {
    padding: 15px;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox */
    box-sizing: border-box;
}

.main-container {
    position: sticky;
    top: 0px;
    padding: 0px;
    border-top: 10px solid #ffc107;
}

dl, ol, ul {
    margin-top: 0;
    margin-bottom: 1rem;
}

.social ul.portfolio li {
    display: inline-block;
}

.social ul.portfolio {
    list-style: none;
}

.social ul.sm {
    margin-top: 1.5em;
    width: 70%;
    list-style: none;
    display: flex;
    justify-content: space-between;
}

.about .title{
    margin: 10px;
}

.p0 {
    padding: 0;
}

.m0a{
    margin: 1em auto;
}

a{
    color: #ffc107;
    cursor: pointer;
    text-decoration: none;
}

.social ul.sm i {
    font-size: 19.8px;
    border: 1px solid;
    padding: 10px;
    border-radius: 50%;
    color: white;
    background: #ffc107;
}

.fa{
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.my-bg {
    background-color: #ffc107!important;
}


li {
    display: list-item;
    text-align: -webkit-match-parent;
}

*, ::after, ::before {
    box-sizing: border-box;
}

.content p {
  text-align: justify;
  text-justify: inter-word;
}

ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

.cloud-tags {
    list-style: none;
    border: 2px solid #ccc;
    padding: 3px;
}

.index-module {
    padding-top:20px;
}

.cloud-tags li {
    display: inline-block;
    margin: 0;
    padding: 0;
    color: #444;
    font-size: 15px;
    line-height: 1.5;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 300;
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
}

.cloud-tags li span {
    display: inline-block;
    padding: 8px;
    text-shadow: 1px 0.01px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    margin-bottom: 2px;
    font-size: 17px;
    text-decoration: none;
    color: #fff;
    transition: .2s;
    outline: 0;
    margin: 1px;
}

.cloud-tags span {
    font-size: 20px;
    color: #000;
}

.parpadea {
  animation-name: parpadeo;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(0, 0.88, 0, 1.1);
  animation-iteration-count: infinite;

  -webkit-animation-name:parpadeo;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: cubic-bezier(0, 0.88, 0, 1.1);
  -webkit-animation-iteration-count: infinite;
}

@-moz-keyframes parpadeo{  
  0% { opacity: 0.0; }
  50% { opacity: 1.0; }
  100% { opacity: 0.0; }
}

@-webkit-keyframes parpadeo {  
  0% { opacity: 0.0; }
  50% { opacity: 1.0; }
  100% { opacity: 0.0; }
}

@keyframes parpadeo {  
  0% { opacity: 0.0; }
  50% { opacity: 1.0; }
  100% { opacity: 0.0; }
}

.project-img {
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    border-bottom: 1px solid #eee;
}

.card-column {
    padding-left:5px;
    padding-right:5px;
}

.all-project-box .card{
    width:100%;
    padding:0;
    margin-bottom:10px;
}

.contact-form-header{
    background-color:#ffc107;
}

.contact-form-btn{
    color:#fff;
    background-color:#ffc107;
    border-color:#ffc107;
}

.contact-form-text{
    color:#ffc107;
}

.project-image{
  background-size: cover;
  width: 100%;
  height: 450px;
  background-repeat: no-repeat;
  background-position: center center;
  margin-bottom: 0.2em;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border: none;
  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.4);
  }
  a {
    color: initial;
    &:hover {
      text-decoration: initial;
    }
  }
  .text-muted i {
    margin: 0 10px;
  }
  p {
    text-align:left;
  }
}

